<template>
  <div class="app">
    <div class="content">
      <h1>邱傲YYDS</h1>
      <div>
        <template class="question" v-for="(question, index) in questions" >
          <question :index="index" :key="index" :question="question" />
        </template>
      </div>
      <div style="margin-top: 20px">
        <el-pagination
          background
          v-show="total>0"
          layout="prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import question from './Question'
import { queryQuestion } from '@/api/system'
export default {
  name: 'HelloWorld',
  components: {
    question
  },

  data:() => {
    return {
      questions: [],
      queryParams: {
        pageNum: 1,
        pageSize: 5
      },
      total: 0
    }
  },
 
  created() {
    this.getQuestion()
  },

  methods: {
    getQuestion () {
      queryQuestion(this.queryParams).then(res => {
      if(res.code === 200) {
        this.questions = res.data
        this.total = res.total
      }
    })
    }
  }
}
</script>

<style scoped>
.app {
}
.content {
  width: 1200px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
</style>
