<template>
  <div class="question">
    <div class="content">
    {{ index + 1 }}、{{ question.qtype === 1 ? '(单选题)': '(多选题)' }}{{ question.question }}
    </div>
    <el-radio-group v-if="question.qtype === 1" v-model="res">
      <template v-for="(option, i) in question.options">
        <div class="answer" :key="i">
          <el-radio class='el-radio' :label="i" >
            {{ i | change }}. {{ option.optionDec }}
          </el-radio>
        </div>
      </template>
    </el-radio-group>

    <el-checkbox-group v-else v-model="res">
      <template v-for="(option, i) in question.options">
        <div  class="answer"  :key="i">
          <el-checkbox :label="i" >
          {{ i | change }}.{{ option.optionDec }}
          </el-checkbox>
        </div>
      </template>
    </el-checkbox-group>
    <div v-show="res.length !== 0">
       <el-alert
        v-if="isRight"
        title="回答正常"
        type="success">
      </el-alert>
      <el-alert
        v-else
        title="回答错误"
        type="error">
      </el-alert>
    </div>
  </div>
</template>
<script>
export default {
  name: 'questionIndex',
  props: {
    question: Object,
    index: Number
  },

  data: () => {
    return {
      res: [],
    }
  },

  filters: {
    change (val) {
      let res = ""
      switch (val) {
        case 0: res = "A"
        break
        case 1: res = "B"
        break
        case 2: res = "C"
        break
        case 3: res = "D"
        break
      }

      return res
    }
  },

  computed:  {
    isRight() {
      let rightIndex = []
      let arr =  this.question.options
      for(let i = 0; i < arr.length; i++) {
        if(arr[i].answer === 1){
          rightIndex.push(i)
        } 
      }
      
      let res = this.res
      if(typeof res === 'number') {
        return rightIndex.includes(res)
      }

      for (let j = 0; j < res.length; j++) {
        if (!rightIndex.includes(res[j])) {
          return false
        }
      }

      return true
    }
  },

  created() {
  }
}
</script>
<style scoped >
.content {
  padding: 20px;
  font-size: 18px;
}

.answer {
  padding: 10px;
}

/deep/.el-radio__input {
    display: none;
}

/deep/.el-checkbox__inner {
  display: none;
}
</style>